"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion"
import { Menu, X, ChevronDown } from "lucide-react"

const navigation = [
  { name: "Home", href: "/" },
  { name: "About", href: "/about" },
  { name: "Academics", href: "/academics" },
  {
    name: "Admissions",
    href: "/admissions",
    children: [
      { name: "Admission Process", href: "/admissions" },
      { name: "Scholarships", href: "/admissions#scholarships" },
    ],
  },
  {
    name: "Media",
    href: "#",
    children: [
      { name: "Gallery", href: "/gallery" },
      { name: "News & Events", href: "/news" },
    ],
  },
  { name: "Contact", href: "/contact" },
]

export function Header() {
  const [isScrolled, setIsScrolled] = useState(false)
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [isVisible, setIsVisible] = useState(true)
  const [lastScrollY, setLastScrollY] = useState(0)

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY

      // Background transparency logic
      setIsScrolled(currentScrollY > 20)

      // Hide/Show navbar logic
      if (currentScrollY > lastScrollY && currentScrollY > 100) {
        setIsVisible(false) // Scroll Down -> Hide
      } else {
        setIsVisible(true)  // Scroll Up -> Show
      }

      setLastScrollY(currentScrollY)
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [lastScrollY])

  return (
    <header
      className={`fixed top-0 left-0 right-0 z-50 ${isMobileMenuOpen
        ? "bg-[#0B2447]" // No transform/transition when menu is open
        : `transition-all duration-300 ${isScrolled ? "bg-[#0B2447]/95 backdrop-blur-md shadow-md" : "bg-[#0B2447]/80 backdrop-blur-sm"
        } ${isVisible ? "translate-y-0" : "-translate-y-full"}`
        }`}
    >
      <nav className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-20 relative">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-3 group">
            <div className="relative w-12 h-12 transition-transform duration-300 group-hover:scale-105">
              <Image
                src="/images/school-20logo-20canary-20academy.png"
                alt="Canary Academy Logo"
                fill
                className="object-contain"
              />
            </div>
            <div className="hidden sm:block">
              <div className="text-xl font-bold text-white leading-tight">Canary Academy</div>
              <div className="text-xs text-[#F5A623] font-medium">Estd. 1999</div>
            </div>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden lg:flex items-center gap-8 absolute left-1/2 -translate-x-1/2">
            {navigation.map((item) =>
              item.children ? (
                <div key={item.name} className="relative group">
                  <DropdownMenu>
                    <DropdownMenuTrigger className="flex items-center gap-1 text-sm font-medium text-white/90 hover:text-[#F5A623] transition-colors outline-none">
                      {item.name} <ChevronDown className="w-4 h-4" />
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="center" className="bg-[#0B2447] border-white/10 text-white min-w-[200px]">
                      {item.children.map((child) => (
                        <DropdownMenuItem key={child.name} asChild className="focus:bg-white/10 focus:text-[#F5A623]">
                          <Link href={child.href} className="w-full cursor-pointer">
                            {child.name}
                          </Link>
                        </DropdownMenuItem>
                      ))}
                    </DropdownMenuContent>
                  </DropdownMenu>
                </div>
              ) : (
                <Link
                  key={item.name}
                  href={item.href}
                  className="text-sm font-medium text-white/90 hover:text-[#F5A623] transition-colors relative group"
                >
                  {item.name}
                  <span className="absolute -bottom-1 left-0 w-0 h-0.5 bg-[#F5A623] transition-all group-hover:w-full" />
                </Link>
              )
            )}
          </div>

          {/* CTA Button */}
          <div className="hidden lg:block">
            <Button asChild className="bg-[#F5A623] hover:bg-[#FFB84D] text-white">
              <Link href="/admissions">Apply Now</Link>
            </Button>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
            className="lg:hidden p-2 text-white hover:text-[#F5A623] transition-colors"
          >
            {isMobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>


      </nav>

      {/* Mobile Menu Overlay */}
      <div
        className={`fixed inset-0 z-40 bg-[#0B2447] transition-all duration-300 lg:hidden flex flex-col pt-32 px-6 ${isMobileMenuOpen ? "opacity-100 visible" : "opacity-0 invisible pointer-events-none"
          }`}
      >
        <div className="flex flex-col gap-6 items-center text-center">
          {navigation.map((item) => (
            <div key={item.name} className="flex flex-col items-center w-full">
              {item.children ? (
                <>
                  <div className="text-xl font-bold text-white mb-4">{item.name}</div>
                  <div className="flex flex-col gap-4 w-full bg-white/5 p-4 rounded-2xl">
                    {item.children.map((child) => (
                      <Link
                        key={child.name}
                        href={child.href}
                        onClick={() => setIsMobileMenuOpen(false)}
                        className="text-lg font-medium text-white/80 hover:text-[#F5A623] transition-colors"
                      >
                        {child.name}
                      </Link>
                    ))}
                  </div>
                </>
              ) : (
                <Link
                  href={item.href}
                  onClick={() => setIsMobileMenuOpen(false)}
                  className="text-2xl font-bold text-white hover:text-[#F5A623] transition-colors"
                >
                  {item.name}
                </Link>
              )}
            </div>
          ))}
          <Button asChild className="bg-[#F5A623] hover:bg-[#FFB84D] text-white w-full max-w-sm mt-4 h-12 text-lg">
            <Link href="/admissions">Apply Now</Link>
          </Button>
        </div>
      </div>
    </header>
  )
}
